.print-in-bl{
    display: inline-block;
    float: none;
}
.print-btn-area{
    display: inline-block;
    float: right;
}
.pre-view-msg{
    float: none !important;
    display: inline-block !important;
    background-image: url(data:image/gif;base64,);
    background-repeat: no-repeat;
    background-position: 10px;
    padding-left: 40px;
}
.print-view-msg{
    float: none !important;
    display: inline-block !important;
    background-image: url(data:image/gif;base64,);
    background-repeat: no-repeat;
    background-position: 10px;
    padding-left: 89px;
    height: 80px;
    line-height: 80px;
    font-size: 28px;
    margin: 30px;
}
.print-view-msg-success{
    float: none !important;
    display: inline-block !important;
    background-repeat: no-repeat;
    background-position: 10px;
    height: 80px;
    line-height: 80px;
    font-size: 28px;
    margin: 30px;
}
.print-btn-area a {
    font-size: 1.13em;
    cursor: pointer;
    width: 43px;
    height: 34px;
    line-height: 34px;
    position: relative;
    right: 0px;
    top: -7px;
    text-align: center;
    padding: 1px;
    display: inline-block;
    border-left: 1px solid #A1A1A1;
    text-decoration: none;
}
.print-btn-area a:hover{
    background: #E0ECFF;
    font-weight: 600;
}
.print-pre-view{
    width: 100%;
    text-align: center;
}


.print-img-loaded{
    display: block;
    margin: 20px auto;
}