.layout {
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  z-index: 0;
}
.layout-panel {
  position: absolute !important;
  overflow: hidden;
}

.x-box-item {

  position: absolute;
left: 0;
top: 0;
}
.layout-panel-east,
.layout-panel-west {
/*   z-index: 2; */
}
.layout-panel-north,
.layout-panel-south {
/*   z-index: 3; */
}
.layout-expand {
  position: absolute;
  padding: 0px;
  font-size: 1px;
  cursor: pointer;
 /*  z-index: 1; */
}
.layout-expand .panel-header,
.layout-expand .panel-body {
  background: transparent;
  filter: none;
  overflow: hidden;
}
.layout-expand .panel-header {
  border-bottom-width: 0px;
}
.layout-split-proxy-h,
.layout-split-proxy-v {
  position: absolute;
  font-size: 1px;
  display: none;
  z-index: 5; 
}
.layout-split-proxy-h {
  width: 5px;
  cursor: e-resize;
    z-index: 5; 
}
.layout-split-proxy-v {
  height: 5px;
  cursor: n-resize;
    z-index: 5; 
}
.layout-mask {
  position: absolute;
  background: #fafafa;
  filter: alpha(opacity=10);
  opacity: 0.10;
/*   z-index: 4; */
}
.layout-button-up {
  background: url('images/layout_arrows.png') no-repeat -16px -16px;
}
.layout-button-down {
  background: url('images/layout_arrows.png') no-repeat -16px 0;
}
.layout-button-left {
  background: url('images/layout_arrows.png') no-repeat 0 0;
}
.layout-button-right {
  background: url('images/layout_arrows.png') no-repeat 0 -16px;
}
.layout-split-proxy-h,
.layout-split-proxy-v {
  background-color: #bbb;
}
.layout-split-north {
  border-bottom: 5px solid #eee;
}
.layout-split-south {
  border-top: 5px solid #eee;
}
.layout-split-east {
  border-left: 5px solid #eee;
}
.layout-split-west {
  border-right: 1px solid #ccc;
}
.layout-expand {
  background-color: #F2F2F2;
}
.layout-expand-over {
  background-color: #F2F2F2;
}
